	isTronLink() {
		// 发现有安装钱包 && 钱包是解锁状态; (等1秒,可能会有延迟导致没有获取到tronWeb对象)
		let promise = new Promise(function(resolve, reject) {
			setTimeout(() => {
				if (window.tronWeb) {
					// 如果没有发默认地址会返回false, 可能是钱包被锁定, 也可能是钱包里面没有地址;
					if (window.tronWeb.defaultAddress.base58) {
						resolve({
							status: true,
							data: "正常"
						});
					} else {
						resolve({
							status: false,
							data: "钱包被锁定或钱包里没有地址"
						});
					}
				} else {
					resolve({
						status: false,
						data: "没有发现波场钱包"
					});
				}
			}, 1000);
		});
		// 处理异步返回结果
		return promise;
	},

	// 创建对象
	async createdObject() {
		this.gTronWeb = window.tronWeb;
		// 创建usdt合约,门票合约,主合约对象
		let masterAddr = this.masterAddress;
		let masterAbi = this.masterAbi;
		let uAddr = this.uAddress;
		let uAbi = this.uAbi;
		let ticketsAddr = this.ticketsAddress;
		let ticketsAbi = this.ticketsAbi;
		this.gMasterContract = await this.gTronWeb.contract(masterAbi, masterAddr);
		this.gTicketsContract = await this.gTronWeb.contract(ticketsAbi, ticketsAddr);
		this.gUSDTContract = await this.gTronWeb.contract(uAbi, uAddr);
	},

