	// 授权usdt给门票合约地址 ====>对应Ecoswap网页 "批准",(批准按钮包含两笔交易，1是授权USDT給门票合约，2是调用门票合约的vending接口购买ticketsToken)
	async approveTickets() {
		console.log('开始授权USDT给门票合约');
		// 进行授权
		let addr = this.ticketsAddress;
		let value = this.usdtAppoveQuota;
		let result = await this.gUSDTContract.approve(addr, value).send({
			feeLimit:1000000000,
			callValue: 0,
			// true就是20块确认才会给返回值, false就是1个确认就给返回值;
			shouldPollResponse: false
		});
		console.log('授权的返回值', result);
		return result;
	},

