	async formatSeconds(value) {
		var secondTime = parseInt(value);// 秒
		var minuteTime = 0;// 分
		var hourTime = 0;// 小时
		if(secondTime > 60) {//如果秒数大于60，将秒数转换成整数
			//获取分钟，除以60取整数，得到整数分钟
			minuteTime = parseInt(secondTime / 60);
			//获取秒数，秒数取佘，得到整数秒数
			secondTime = parseInt(secondTime % 60);
			//如果分钟大于60，将分钟转换成小时
			if(minuteTime > 60) {
				//获取小时，获取分钟除以60，得到整数小时
				hourTime = parseInt(minuteTime / 60);
				//获取小时后取佘的分，获取分钟除以60取佘的分
				minuteTime = parseInt(minuteTime % 60);
			}
		}
		var result = "" + parseInt(secondTime) + ":";

		if(minuteTime > 0) {
			result = "" + parseInt(minuteTime) + ":" + result;
		}
		if(hourTime > 0) {
			result = "" + parseInt(hourTime) + ":" + result;
		}
		return result;
	},
	//获取用户数据
	async userData(_addr) {
		let res = await this.gMasterContract.accounts(_addr).call();
		let nodeLevel = res.nodeLevel._hex;////====>对应主网页 "V1/V2/V3/V4",1=>V1,2=>V2,3=>V3,4=>V4
		let joinTime = res.joinTime._hex;
		let nowTimeSec=Date.parse(new Date());
		let sec100Days = 86400*100;
		let countdown100DaySec = (parseInt(joinTime)+sec100Days)-nowTimeSec;
		let cd100DayString = await this.formatSeconds(countdown100DaySec);//====>对应主网页本部分 第一行的"倒计时"
		let dayPoolIncome = res.dayPoolIncome._hex;//====>对应主网页 "日奖池收益"
		let weekPoolIncome = res.weekPoolIncome._hex;//====>对应主网页 "周奖池收益"
		let dynamicIncome = res.dynamicIncome._hex;//====>对应主网页 "动态收益"
		let reward = res.reward._hex;//====>对应主网页 "分红收益"
		let oneDaySec = 86400;
		let countdownTime  = oneDaySec - (nowTimeSec-joinTime)%oneDaySec;
		let cdTimeString = await this.formatSeconds(countdownTime);//====>对应主网页 "下次分红倒计时时分秒"
		let deposit = res.deposit._hex;//====>对应主网页 "存入资金"
		let maxIncome = res.maxIncome._hex;
		let AcceleratedExit = parseInt(maxIncome)/parseInt(deposit);//====>对应主网页 "加速出局差"
		let untakeIncome = res.income._hex;//====>对应主网页 "未提现收益"
		return res;
	},
